	module.exports = {"data":{"markdownRemark":{"html":"<p>National Song and Dance Food City는 고대 황제의 삶과 마찬가지로 민족 노래와 춤 공연, 한쪽에서의 식사, 다른 한 공연에서의 공연을 특징으로합니다. 가장 인기있는 쇼는 무대에 오르고, Hada를받으며, 함께 춤을 추는 등의 이름을 가진 손님을 초대하는 것입니다. 분위기가 있습니다.</p>\n<p>이 장소는 원래 전국가요 무용단의 리허설 홀이었으며 지금은 식당이되었고 그 앞에 많은 공간이 있으며 아마도 리허설을 할 수 있습니다. 의심의 여지없이 이것은 시장 경제의 조류의 현상이다. 노래와 무용단의 리허설 홀은 호텔이되었고 중국 사회의 발전을 상징하는 상징이기도합니다. 그러나 그것이 상향 개발인지 또는 어떤 종류의 개발인지를 말하기는 어렵습니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0119.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0119.md","frontmatter":{"title":"7월 8일 금요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0119.md absPath of file >>> MarkdownRemark"}}

